<template>
<div class="goods-arctile">
	<div class="goods-arctile-content">
		<div class="biger-img">
			<img :src="`http://localhost:3000${articleDate.preview}`" :alt="`${articleDate.name}`">
		</div>
		<div class="info">
			<div class="name">{{articleDate.name}}</div>
			<div class="describe">{{articleDate.describe}}</div>
			<div class="price">￥{{articleDate.price}}<el-button class="add-to-cart fr" @click="AddIdToCart" type="info">加入购物车</el-button></div>
			
		</div>
		<div class="b-hr"></div>
		<div class="comment">
			<div class="c-h">
				<span class="fl">评价(<em>83690</em>)</span>
				<a class="fr" href="javascript:;">好评<em>99%</em></a>
			</div>
			<div class="comment-list">
				<div class="comment-list-item">
					<div class="comment-item-info">
						<div class="full-star fl"><div class="score-start" style="width: 87%;"></div><!-- <span class="score pa">8.7</span> --></div>
						<div class="comment-item-author fr">j***g</div>
					</div>
					<div class="comment-item-content">
						公牛大牌子，值得信赖，家里的几乎都是用的公牛的。京东今天买，明天到，省去了去五金店的时间。
					</div>
				</div>
				<div class="comment-list-item">
					<div class="comment-item-info">
						<div class="full-star fl"><div class="score-start" style="width: 87%;"></div><!-- <span class="score pa">8.7</span> --></div>
						<div class="comment-item-author fr">j***g</div>
					</div>
					<div class="comment-item-content">
						公牛大牌子，值得信赖，家里的几乎都是用的公牛的。京东今天买，明天到，省去了去五金店的时间。
					</div>
				</div>
				<div class="comment-list-item">
					<div class="comment-item-info">
						<div class="full-star fl"><div class="score-start" style="width: 87%;"></div><!-- <span class="score pa">8.7</span> --></div>
						<div class="comment-item-author fr">j***g</div>
					</div>
					<div class="comment-item-content">
						公牛大牌子，值得信赖，家里的几乎都是用的公牛的。京东今天买，明天到，省去了去五金店的时间。
					</div>
				</div>
			</div>
		</div>
	</div>
	<LRModel :LoginShow="LoginShow" :RegShow="RegShow" v-on:ChangeLoginShow="ChangeLoginShow"></LRModel>
	<tfooter></tfooter>
</div>
</template>
<script>
    import { tfooter,LRModel } from '../components/'
    import { mapMutations } from 'vuex'
    import cookies from 'js-cookie'
	export default {
		data(){
			return {
				id : '',
				LoginShow : false,
				RegShow : false,
                articleDate : {}
			}
		},
		created(){
	        var id = this.$route.path.replace(/\/article\//,''),self = this;
	        this.id = id;
	        this.$store.dispatch('getGoodByIdAsync',{
	        	id : id,
	        	cb : function (data) {
	        		data.body.data.preview = data.body.data.preview.replace(/small/,'biger');
	        		self.articleDate = data.body.data; 
	        	}
	        })
	        
	    },
	    methods : {
           AddIdToCart(){
           	   this.$store.dispatch('addToCart',{
               	   id : this.id,
               	   cb : function (data) {
               	   	  console.log(data)
               	   }
               })
           },
           ChangeLoginShow(a,b,u){
				this.LoginShow = a;
				this.RegShow = b
				if(u){
					this.user.name = u
				}
			}
	    },
	    components: {
	    	tfooter,
	    	LRModel
	    }
	}
</script>
<style>
.pr {
    position: relative;
}

.fl, .fr {
    display: inline;
}

.fl {
    float: left;
}
.fr {
    float: right;
}
img{ padding: 0; margin: 0; }
.goods-arctile{ display: flex; flex-direction: column; width: 100%; height: 100%; overflow: hidden; }
.goods-arctile-content{ flex: 1;-webkit-overflow-scrolling : touch; overflow: auto; }
.goods-arctile .biger-img{ border-bottom: 1px solid #f1f2f4; width: 100%; }	
.goods-arctile .biger-img img{ width: 100%; vertical-align: middle; }
.goods-arctile .info{ padding: 10px; color: #333; width: 100%; }
.goods-arctile .b-hr{ height: 10px; background: #f0f2f5; border-top: 1px solid #e9ebef; border-bottom: 1px solid #e9ebef;}
.goods-arctile .info .name{ font-size: 16px; margin-bottom: 3px; padding: 0 5px;}
.goods-arctile .info .describe{ font-size: 14px;  padding: 0 5px;}
.goods-arctile .info .price{padding: 8px 0; font-size: 24px; color: #f23030; }
.comment{ padding: 0 10px; }
.comment .c-h{ height: 46px; line-height: 46px; color: #81838e; border-bottom: 1px solid #f1f1f1;}
.comment .c-h a{  color: #81838e; }
.comment .c-h em{ font-style: normal; }

.comment-list-item{ padding: 0 0 10px 0; border-bottom: 1px solid #f1f1f1;}
.comment-item-info{ height: 35px; line-height: 35px; color: #f23030; }
.full-star {
  margin-top: 10px;
  width: 70px;
  height: 14px;
  line-height: 20px;
  background-size: auto 100%;
  color: #f15353;
  background-image: url('../assets/images/nostart.svg');
}
.score-start {
	width: 100%;
	height: 100%;
	background-size: auto 100%;
	background-image: url('../assets/images/star.svg');
}
.full-star .score{ font-size: 12px; }
.comment-item-author{ color: #848689; font-size: 12px; }
.comment-item-content{ color: #333; padding-top: 0px; font-size: 13px; line-height: 20px; }
</style>


