import { NOTIFI ,GETGOODSLIST } from './type'
import { mapMutations } from 'vuex'
import api from '@/api/'
import Vue from 'vue'

const actions = {
    async ['insertGoodAsync'] ({commit, state}, config) {
        const data = await api.insertGoodAsync(config)
        const obj = {
           data,
           cb : config.cb
        }
        if (data) {
            commit(NOTIFI, obj);
        }
    },
    async ['updateGoodAsync'] ({commit, state}, config) {
        const data = await api.updateGoodAsync(config)
        const obj = {
           data,
           cb : config.cb
        }
        if (data) {
            commit(NOTIFI, obj);
        }
    },
    async['createGoodType'] ({commit, state} , config) {
        const data = await api.insertGoodTypeAsync(config)
        const obj = {
           data,
           cb : config.cb
        }
        if (data) {
            commit(NOTIFI, obj);
        }
    },
    async['getGoodType'] ({commit, state} , config) { 
        const data = await api.getGoodType(config)

        const obj = {
           data,
           cb : config.cb
        }
        if (data) {
            commit(NOTIFI, obj);
        }
    },
    async['getGoodByTypeIdAsync'] ({commit, state} , config) { 
        const data = await api.getGoodByTypeIdAsync(config)
        const obj = {
           data,
           cb : config.cb
        }
        if (data) {
            commit(GETGOODSLIST, obj);
        }
    },
    
    async['getByIdGroup'] ({commit, state} , config) { 
        const data = await api.getByIdGroup(config)
        console.log('vv',data)
        const obj = {
           data,
           cb : config.cb
        }
        if (data) {
            commit(GETGOODSLIST, obj);
        }
    },
    async['getGoodByIdAsync'] ({commit, state} , config) { 
        const data = await api.getGoodByIdAsync(config)
        const obj = {
           data,
           cb : config.cb
        }
        if (data) {
            commit(GETGOODSLIST, obj);
        }
    },
    async['deleteGoods'] ({commit, state} , config) {
        const data = await api.deleteGoodseAsync(config)
        const obj = {
           data,
           cb : config.cb
        }
        if (data) {
            commit(NOTIFI, obj);
        }
    },
    async['showGoodsList'] ({commit, state} , config) { 
        const data = await api.showGoodsListAsync(config)
        const obj = {
           data,
           cb : config.cb
        }
        if (data) {
            commit(GETGOODSLIST, obj);
        }
    }
}

export default actions



