<template>	
	<section ref="city" id="select-city" class="pf fadeInDown" v-if="$store.state.city.show">
		<header class="city-header mint-1px-b pr">
			<span class="fb">选择城市</span>
			<span class="close-city pa" @click="closeCityList">×</span>
		</header>
		<div ref="city">
			<mt-index-list>
			 <!--  <mt-index-section :index="city.sort" v-for="city in cityList">
			    <mt-cell :title="name.regionName" v-for="name in city.data" key="name.id"></mt-cell>
			  </mt-index-section> -->
			</mt-index-list>
		</div>
	</section>
</template>
<script>
import { mapActions, mapMutations } from 'vuex'
export default {
   data(){
   	  return {
   	  	  showCityList: true,
		  cityList: []
	  }
	},
	methods: {
		...mapActions([
			'updateCityAsync'
		]),
		...mapMutations([
		  'pushLoadStack',
		  'completeLoad'
		]),
		closeCityList () {
			this.changeCityData({city: {}})
		},
		changeCityData (data){
			this.$refs.city.className = "pf fadeOutTop"
			this.$store.dispatch('updateCityAsync', data).then(this.completeLoad)
		}
	},
	created () {
		
	}
}
</script>
<style>
.mint-indicator-wrapper {
	z-index: 1000
}
#select-city {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 9999999;
	background-color: #fff;
}
.city-header {
	height: 46px;
	line-height: 46px;
	text-align: center;
	color: #000;
	font-size: 16px;
	background-color: #fff;
}
.close-city {
	font-size: 28px;
	color: #666;
	display: inline-block;
	height: 46px;
	width: 50px;
	line-height: 38px;
	text-align: center;
	right: 0px;
}
@-webkit-keyframes fadeInDown {
  0% {
    opacity: .7;
    -webkit-transform: translateY(-50px);
    transform: translateY(-50px)
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0)
  }
}
@keyframes fadeInDown {
  0% {
    opacity: .7;
    -webkit-transform: translateY(-50px);
    -ms-transform: translateY(-50px);
    transform: translateY(-50px)
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0)
  }
}
.fadeInDown {
  -webkit-animation: fadeInDown .3s;
  animation: fadeInDown .3s;
}
@-webkit-keyframes fadeOutTop {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0)
  }
  100% {
		opacity: 0;
		-webkit-transform: translateY(-50px);
		transform: translateY(-50px)
  }
}
@keyframes fadeOutTop {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0)
  }
  100% {
	  opacity: 0;
	  -webkit-transform: translateY(-50px);
	  -ms-transform: translateY(-50px);
	  transform: translateY(-50px)
  }
}
.fadeOutTop {
  -webkit-animation: fadeOutTop .35s;
  animation: fadeOutTop .35s;
}
</style>


