import { ADDIDTOCART } from './type'
import { mapMutations } from 'vuex'
import api from '@/api/'
import Vue from 'vue'

const actions = {
    async['addToCart'] ({commit, state}, config) {
        if (config) { 
            //const data = await api.addToCart(config)
            const obj = {
               id : config.id,
               cb : config.cb
            }
            commit(ADDIDTOCART, obj);
        }
    },
    async['getCartId'] ({commit, state}, config) {
        if (config) { 
            const data = await api.getCartId(config)
            const obj = {
               data,
               cb : config.cb
            }
            if (data) {
               commit(ADDIDTOCART, obj);
            }
        }
    },
}

export default actions



