<template>
<div class="buy-cart">
	<div class="header">购物车</div>
	<div class="buy-cart-list">
		<ul>
	  		<li v-for="(item, index) in userBuyGoodItem">
	  			<a href="javascropt:;">
	  				<div class="dogo"><i></i></div>
		  			<div class="img"><img :src="`${item.preview}`" :alt="item.name"></div>
		  			<div class="info">
		  				<div class="t-n">{{item.name}}</div>
		  				<div class="describe">{{item.describe}}</div>
		  				<div class="price">￥{{item.price}}</div>
		  			</div>
	  			</a>
	  		</li>
	  	</ul>
	</div>
</div>	
</template>
<script>
export default {
	data(){
		return {
            userBuyGoodItem : []
		}
	},
	created(){
		var self = this;
		if(window.localStorage.getItem('userBuyGoodItem')){
           var k = JSON.parse(window.localStorage.getItem('userBuyGoodItem')).item
           this.$store.dispatch('getByIdGroup',{
           	BuyGoodsList : k,
           	cb : function (data) {
           		self.userBuyGoodItem = data.body.data[0]
           	}
           })
        }
	}
	
}
</script>
<style>
.buy-cart{
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	background: #f7f7f7;
}
.dogo{ width: 30px; height: 88px;}
.dogo i{ display: block; width: 15px; height: 15px; border: 1px solid #9e9e9e;  border-radius: 50%; margin: 38px auto 0}
.dogo.on i{background: #e93b3d;}
.buy-cart .header{ background: #fff; height: 42px; line-height: 42px; margin-bottom: 10px; text-align: center; border-bottom: 1px solid #ededed; font-size: 18px; }
.buy-cart-list{ height: 100%;  -webkit-overflow-scrolling : touch; overflow: auto; }
.buy-cart-list ul li{ border-top: 1px solid #ededed; border-bottom: 1px solid #ededed; background: #fff; margin-bottom: 10px;}
.buy-cart-list ul li a{ margin: 0 0; padding: 10px 15px 5px 0px; display: flex; } 
.buy-cart-list ul li .img{ width: 88px; margin-right: 10px;}
.buy-cart-list ul li .img img{ width: 100%; }
.buy-cart-list ul li .info{ flex: 1;}
.buy-cart-list .t-n{
	overflow : hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 1;
	-webkit-box-orient: vertical;
	color: #333;
	font-size: 16px;
}
.buy-cart-list .describe{
	overflow : hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	color: #9c9c9c;
	line-height: 18px;
}
.buy-cart-list .price{ color: #DD2727; font-size: 16px;}
</style>


