<template>
	<div class="Dailog">
		<at-modal width="300" v-model="LShow" title="登录" @on-cancel="onCancel1">
			<div class="ii mb20"><at-input class="username" v-model="username" placeholder="用户名"></at-input></div>
			<div class="ii mb20"><at-input class="password" v-model="password" type="password" placeholder="设置密码"></at-input></div>
			<div class="ii clearfix mb20"><at-input class="code" v-model="code" placeholder="验证码"></at-input><img @click="getRegcode" class="codeMsg" :src="ecsrc"></div>
			<div slot="footer">
				<at-button style="width:100%;" type="primary" @click="login">确 定</at-button>
				<div class="t-reg" @click="onCancel4">快速注册</div>
			</div>
		</at-modal>
		<at-modal width="300" v-model="RShow" title="注册" @on-cancel="onCancel2">
			<div class="ii mb20"><at-input class="username" v-model="r_username" placeholder="用户名"></at-input></div>
			<div class="ii mb20"><at-input class="password" v-model="r_password" type="password" placeholder="设置密码"></at-input></div>
			<div class="ii mb20"><at-input class="confirm" v-model="r_confirm" type="password" placeholder="确认密码"></at-input></div>
			<div class="ii clearfix mb20"><at-input class="code" v-model="r_code" placeholder="验证码"></at-input><img @click="getRegcode" class="codeMsg" :src="r_ecsrc"></div>
			<div slot="footer">
				<at-button style="width:100%;" type="primary" @click="register">确 定</at-button>
				<div class="t-login" @click="onCancel3">快速登录</div>
			</div>
		</at-modal>
	</div>
</template>
<script>
export default {
   props : ['LoginShow','RegShow'],
   data(){
   	   return {
   	   	   username : '',
   	   	   password : '',
   	   	   code : '',
   	   	   ecsrc : '',
   	   	   r_username : '',
   	   	   r_password : '',
   	   	   r_confirm : '',
   	   	   r_code : '',
   	   	   r_ecsrc : ''
   	   }
   },
   computed : {
   	    LShow : {
   	    	set(){
	         
	        },
	        get(){
	           return  this.LoginShow;
	        }
        },
        RShow : {
        	set(){
	           
	        },
	        get(){
	           return  this.RegShow;
	        }
            
        }
   },
   mounted(){
   	   var self = this;

   	   this.$store.dispatch('captchas',{
	   	   	cb : function (data) { 
	   	   		var src = data.cpng.data.data;
	   	   		var img = new Image();
	   	   		img.src = src;
	   	   		self.r_ecsrc = '';
	   	   		self.ecsrc = '';
	   	   		img.onload = function () {
	   	   			self.ecsrc = data.cpng.data.data; 
	   	   			self.r_ecsrc = data.cpng.data.data;
	   	   			img = null;
	   	   		} 
	   	   	}
   	   })
   },
   methods : {
		register(){
			var self = this;
           this.$store.dispatch('insertFrontUsers',{
              username : self.r_username,
              password : self.r_password,
              confirm : self.r_confirm,
              code : self.r_code,
              cb : function (data) {
              	  if(self.$store.state.users.usersNotifi.code == 200){
                   	  self.r_username = "";
                   	  self.r_password = "";
                   	  self.r_confirm = "";
                   	  self.r_code = "";
                   	  self.onCancel3()
                 }else{
                 	  self.getRegcode();
                 }
              	  self.$Message.warning({ message: self.$store.state.users.usersNotifi.message })
              }
           })
		},
		login(){
			var self = this;
           this.$store.dispatch('frontUsersLogin',{
              username : self.username,
              password : self.password,
              code : self.code,
              cb : function (data) {
              	  if(self.$store.state.users.usersNotifi.code == 200){  
                      self.onCancel1(self.username);
                      self.username = "";
                      self.password = "";
                      self.code = "";
                 }else{
                 	  self.getRegcode();
                 }
              	  self.$Message.warning({ message: self.$store.state.users.usersNotifi.message })
              }
           })
		},
		getRegcode(){ 
			var self = this;
			this.$store.dispatch('captchas',{ 
				cb : function (data) { 
					var src = data.cpng.data.data;
					var img = new Image();
					img.src = src;
					self.r_ecsrc = '';
					self.ecsrc = '';
					img.onload = function () { 
						self.r_ecsrc = data.cpng.data.data;
						self.ecsrc = data.cpng.data.data;
						img = null;
					}
				    
				}
			})
		},
		onCancel1(u){
			this.$emit('ChangeLoginShow',false,false,u);
           //console.log(this.LoginShow)
		},
		onCancel2(){
			this.$emit('ChangeLoginShow',false,false);
           //this.RegShow = false;
		},
		onCancel3(){
			this.$emit('ChangeLoginShow',true,false);
		},
		onCancel4(){
			this.$emit('ChangeLoginShow',false,true);
		}
   }
}
</script>
<style>
.Dailog .t-reg,.Dailog .t-login{ margin-top: 10px; color: #333; }
.Dailog .mb20{ margin-bottom: 10px; }
.Dailog .code{ width: 158px; float: left; }
.Dailog .codeMsg{ float: right; width:100px; height: 32px; }
</style>


