import Vue from 'vue'



const insertAdminUsers = function (config) { return Vue.http.post('/api/users/admininsert',config); }
const adminUserLoginAsync = function (config) { return Vue.http.post('/api/users/adminLoginAsync',config); }



const insertGoodAsync = function (config) { return Vue.http.post('/api/goods/insertGoodAsync',config); }
const updateGoodAsync = function (config) { return Vue.http.post('/api/goods/updateGoodAsync',config); }
const getGoodType = function (config) { return Vue.http.post('/api/goods/getGoodType',config); }
const getGoodByTypeIdAsync = function (config) { return Vue.http.post('/api/goods/getGoodByTypeIdAsync',config); }
const getGoodByIdAsync = function (config) { return Vue.http.post('/api/goods/getGoodByIdAsync',config); }
const getByIdGroup = function (config) { return Vue.http.post('/api/goods/getByIdGroup',config); }
const addToCart = function (config) { return Vue.http.post('/api/goods/addToCart',config); }
const getCartId = function (config) { return Vue.http.post('/api/goods/getCartId',config); }


const insertGoodTypeAsync = function (config) { return Vue.http.post('/api/goods/insertGoodTypeAsync',config); }
const deleteGoodseAsync = function (config) { return Vue.http.post('/api/goods/deleteGoodseAsync',config); }
const showGoodsListAsync = function (config) { return Vue.http.post('/api/goods/showGoodsListAsync',config); }

const insertFrontUsers = function (config) { return Vue.http.post('/api/users/insertFrontUsers',config); }
const frontUsersLogin = function (config) { return Vue.http.post('/api/users/frontUsersLogin',config); }





const captchas = function (config) { return Vue.http.post('/api/users/captchas',config); }






export default {
	'adminUserLoginAsync' : adminUserLoginAsync,
	'insertAdminUsers' : insertAdminUsers,
	'insertGoodAsync' : insertGoodAsync,
	'insertGoodTypeAsync' : insertGoodTypeAsync,
	'deleteGoodseAsync' : deleteGoodseAsync,
	'showGoodsListAsync' : showGoodsListAsync,
	'updateGoodAsync' : updateGoodAsync,
	'getGoodType' : getGoodType,
	'getGoodByTypeIdAsync' : getGoodByTypeIdAsync,
	'getGoodByIdAsync' : getGoodByIdAsync,
	'insertFrontUsers' : insertFrontUsers,
	'frontUsersLogin' : frontUsersLogin,
	'captchas' : captchas,
	'getByIdGroup' : getByIdGroup,
	'addToCart' : addToCart,
	'getCartId' : getCartId
}


