<template>
  <section class="wrap">
        <div class="baner"><ads></ads></div>
        <el-col class="mian cc" :span="24">
           <el-tabs class="main-TAB" :span="24" v-model="activeName" @tab-click="TabHandleClick">
                  <el-tab-pane
                    v-for="(item, index) in TypeTabsData"
                    :key="item._id"
                    :label="item.type"
                  >
                    <!-- <tabcomponent :index="index" :childIndex="index" :component="item.component"></tabcomponent> -->
                   <frontGoodsList :id="item._id" :type="item.type"></frontGoodsList>
                  </el-tab-pane>
             
           </el-tabs>
       </el-col>
       <tfooter></tfooter>
       
  </section>
</template>
<script>
  import { ads,tfooter } from '../components/'
  import frontGoodsList from './front-goods-list'
  import { mapMutations } from 'vuex'
  
  
	export default {
		data(){
			return {
          activeName : 0,
          TT : [],
          TypeTabsData :[]
			}
		},
	  created(){
        var self = this;
        this.$store.dispatch('getGoodType',{
            cb : function (data) {   
              if(data.data.body.code === 200){
                self.TypeTabsData = data.data.body.data.list.reverse();
                for (var i = 0; i <  self.TypeTabsData.length; i++) {
                   self.TT[i] = 1;
                }
              }
            }
        })
    },
    methods : {
        TabHandleClick(tab, event){
            //console.log(this.activeName);
        }
    },
		components: {
       frontGoodsList,
       ads,
       tfooter
		},
	}
</script>
<style>



.pr {
    position: relative;
}

.fl, .fr {
    display: inline;
}

.fl {
    float: left;
}
.fr {
    float: right;
}

.wrap{width: 100%; height: 100%; display: flex; flex-direction: column; overflow: hidden;}
.mian.cc{ flex: 1; }
.main-TAB .el-tabs__nav{ width: 100%; display: flex; font-size: 12px; height: 40px; line-height: 40px; border-bottom: 1px solid #ededed; -webkit-overflow-scrolling : touch; overflow: auto; }
.main-TAB .el-tabs__nav .el-tabs__item{  flex:1; text-align: center; color: #333; min-width: 80px; }
.mian{ width: 100%;}
.main-TAB{ width: 100%; height: 100%; display: flex; flex-direction: column; }
.main-TAB .el-tabs__header{ margin-bottom: 0px; }
.main-TAB .el-tabs__content{ height: 100%; flex:1; height: 1px;}
.main-TAB .el-tabs__content .el-tab-pane{ height: 100%; }
.footer-bar{ width: 100%; height: 48; line-height: 48px; color: #333; border-top: 1px solid #ededed; }
.quit{ padding: 0 20px; float: right; }
.main-cont{ padding: 0 0; }
.main-cont .el-tabs__active-bar{ background-color: #000; }
.page-loading{ top: 50%; width: 100%; position: fixed;}


</style>


