import { UERSISERT , ADMINLOGIN,CODETOPNG} from './type'
import { mapMutations } from 'vuex'
import api from '@/api/'
import Vue from 'vue'

const actions = {
    async ['captchas'] ({commit, state}, config) { 
        const {data} = await api.captchas(config)
        if (data) {
            const obj = {
               data,
               cb : config.cb
            }
            commit(CODETOPNG, obj);
            
        }
    },
    async ['insertAdminUsers'] ({commit, state}, config) { 
        const {data} = await api.insertAdminUsers(config)
        if (data) {
            commit(UERSISERT, data);
            config.cb(state);
        }
    },
    async ['insertFrontUsers'] ({commit, state}, config) { 
        const {data} = await api.insertFrontUsers(config)
        if (data) {
            commit(UERSISERT, data);
            config.cb(state);
        }
    },
    async ['frontUsersLogin'] ({commit, state}, config) { 
        const {data} = await api.frontUsersLogin(config)
        if (data) {
            commit(ADMINLOGIN, data);
            config.cb(state);
        }
    },
    async ['adminUserLoginAsync'] ({commit, state}, config) { 
        const {data} = await api.adminUserLoginAsync(config)
        if (data) {
            commit(ADMINLOGIN, data);
            config.cb(state);
        }
    },
    async ['FrontUserLoginAsync'] ({commit, state}, config) { 
        const {data} = await api.adminUserLoginAsync(config)
        if (data) {
            commit(ADMINLOGIN, data);
            config.cb();
        }
    }
}

export default actions



