import { UERSISERT,ADMINLOGIN,CODETOPNG } from './type'
const mutations = {
    [CODETOPNG](state, data) { 
          if (data) {
              state.cpng = data
              data.cb(state);
              //return Object.assign({}, state, {Notifi:data});
          }
    },
    [UERSISERT](state, data) { 
          if (data) {
              state.usersNotifi=data;
              
              //return Object.assign({}, state, {Notifi:data});
          }
    },
    [ADMINLOGIN](state, data) { 
          if (data) {
              state.usersNotifi=data;
              state.userName = '';
	          state.userPwd = '';
	          state.email = '';
              //return Object.assign({}, state, {Notifi:data});
          }
    },
}
export default mutations


