import Vue from 'vue'
import Router from 'vue-router'

import BackendHome from '@/pages/backend-home'
import BackendLogin from '@/pages/backend-login'
import FrontGoodsArticle from '@/pages/front-goods-article'
import FrontUserCenter from '@/pages/front-user-center'
import Cart from '@/pages/cart'
import Home from '@/pages/home'
import cookies from 'js-cookie'

const inBrowser = typeof window !== 'undefined'

Vue.use(Router)


const guardRoute = (to, from, next) => {
    var token = cookies.get('b_user') || !inBrowser
    if (!token) {
        next('/backend/login')
    } else {
        next()
    }
}

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/backend/',
      name: '后台登陆',
      component: BackendLogin
    },
    {
      path: '/backend/login',
      name: '后台登陆',
      component: BackendLogin
    },
    {
      path: '/backend/home',
      name: '后台主页',
      component: BackendHome,
      beforeEnter: guardRoute 
    },
    {
      path: '/',
      name: '首页',
      component: Home
    },
    {
      path: '/article/:id',
      name: '文章',
      component: FrontGoodsArticle
    },
    {
      path: '/user',
      name: '用户中心',
      component: FrontUserCenter
    },
    {
      path: '/cart',
      name: '购物车',
      component: Cart
    }
  ]
})



